import React from 'react';
import Media from 'react-media';
import { ReactSVG } from 'react-svg';

class Courses extends React.Component
{
    state = {
        courseList: [
            {
                name: "Java Programming MasterClass",
                institute: "Learn Programming",
                logo: require("../svgs/LP.svg")
            },
            
            {
                name: "Deep Learning Specialization",
                institute: "deeplearning.ai",
                logo: require("../svgs/deeplearning.svg")
            },

            {
                name: "Tensorflow Developer",
                institute: "deeplearning.ai",
                logo: require("../svgs/deeplearning.svg")
            },

            {
                name: "NLP Specialization",
                institute: "deeplearning.ai",
                logo: require("../svgs/deeplearning.svg")
            },

            {
                name: "Applied DataScience Specialization",
                institute: "deeplearning.ai",
                logo: require("../svgs/michigan.svg")
            },
        ]
    }

    renderCourseList = (courseList) => {
        return courseList.map((course, index) => {
            return (
                <Media query={{maxWidth: 800}} >
                    {
                        mobile => {
                            return (
                                <div className={`course ${mobile ? "small": ""}`}>
                                    <ReactSVG beforeInjection={svg => svg.classList.add("course-logo")} src={course.logo} />                          
                                    <div className="course-body">
                                        <div className="text-large text-whitesmoke"> { course.name } </div>
                                        <div className="text-small text-neon text-italic">@  { course.institute } </div>
                                    </div>
                                </div>
                            );
                        }
                    }
                </Media>
            );
        });
    }

    render()
    {
        return (
            <div className="tab-content">
                <div id="courses" >
                    { this.renderCourseList(this.state.courseList) }
                </div>
            </div>
        );
    }
}

export default Courses;